@import url("https://fonts.googleapis.com/css2?family=Rubik&display=swap");
.main{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    font-family: Rubik;
    font-weight: 700;
    background-color: #2c1338;
    height: 90vh;
    width: 100%;
    display: flex;
    border: 1px solid red;
}
.homeintro{
    width: 50%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: flex-start;
    margin-left: 100px;
    font-size: 50px;
    color: white;
    border: 1px solid teal;
}
.homeimage{
    width: 50%;
    /* height: 80vh; */
    border: 2px solid teal;
    position: relative;
}
.bg{
    background-color: #644299;
    /* border: 10px solid red; */
    height:300px;
    width: 300px;
    border-radius: 100%;
}
.profileimage{
    /* margin: 50px auto; */
    /* height: 80vh;  */
    border: 2px solid red;
    border-radius: 100%;
    position: absolute;
    top: 20px;
}
.title{
    animation-name: moveRight;
    animation-duration: 0.7s;
    color: #fff3ed;
}
.titleTwo{
    animation-name: moveLeft;
    animation-duration: 1s;
    color: #fff3ed;
}
.titleThree{
    animation-name: moveRight;
    animation-duration: 1.3s;
    color: #dd6fd1;
    margin-bottom: 10px;
}
.text{
    font-size: 40px;
    color: #ffffff;
    margin-bottom: 20px;
}
.button{
    font-size: 16px;
    border: 2px solid white;
    border-radius: 5px;
    outline: none;
    background-color: #11cf90;
    color: black;
    padding: 12px 25px;
    position: relative;
    transform: translateZ(0);
    animation-name: moveUp;
    animation-duration: 1s;
    cursor: pointer;
}
.button::before{
    content: "";
    position: absolute;
    z-index: -1;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background-color: #eeff00;
    transform: scaleX(0);
    transition: 300ms ease-out;
}
.button:hover::before{
    transform: scaleX(1);
}
@keyframes moveRight{
    0%{
        opacity: 0;
        transform:translateX(100px)
    }
    100%{
       opacity: 1;
       transform: translate(0);
    }
}
@keyframes moveLeft{
    0%{
        opacity: 0;
        transform: translateX((-150px));
    }
    100%{
        opacity: 1;
        transform: translate(0);
    }
}
@keyframes moveUp{
    0%{
        opacity: 0;
        transform: translateY((50px));
    }
    100%{
        opacity: 1;
        transform: translate(0);
    }
}
@media screen and (max-width:1100px) {
    .homeintro{
        width: 100vw;
        height: 80vh;
        display: flex;
        justify-content: center;
        font-size: 50px;
        margin: 0 auto;
        padding-left: 80px;
    }
    .homeimage{
        display: none;
    }
    .button{
        padding: 10px 20px;
    }
}
@media screen and (max-width:800px) {
    .homeintro{
        font-size: 40px;
        padding-left: 40px;
    }
    .homeimage{
        display: none;
    }
    .button{
        padding: 8px 16px;
    }
}
@media screen and (max-width:600px) {
    .homeintro{
        font-size: 30px;
    }
    .text{
        font-size: 24px;
    }
    .button{
        padding: 6px 12px;
    }
}